<template>
  <div :class="{'interchange-container-pc': isPc, 'interchange-container-mb': !isPc}">
    <div class="success-content">
      <div :class="{'steps-list': isLogin, 'steps-list-logged': !isLogin}" v-if="!isRenew">
        <div class="steps-icon">
          <div class="pay-radio hook number">
            <i v-if="isLogin || paySuccess"></i>
            <span v-if="!isLogin && !paySuccess">{{ 1 }}</span>
          </div>
          <div class="pay-radio hook number" v-if="isLogin">
            <i v-if="paySuccess"></i>
            <span v-if="!paySuccess">{{ 2 }}</span>
          </div>
          <div class="pay-radio number" :class="{'pay-radio-gray' : !paySuccess}">
            <span>{{ !isLogin ? 2 : 3 }}</span>
          </div>
        </div>
        <div class="step-lines">
          <div class="blue-line" :class="{'gray-line': !isLogin && !paySuccess}"></div>
          <div class="blue-line" :class="{'gray-line': !paySuccess}"></div>
        </div>
        <!--<div class="steps-line"></div>-->
        <div class="steps-icon steps-tips">
          <span>{{ isLogin ? $t('order/pay.Step-1') : '支付订单' }}</span>
          <span v-if="isLogin">{{ $t('order/pay.Step-2') }}</span>
          <span>{{ $t('order/pay.Step-3') }}</span>
        </div>
      </div>
      <div class="pay-success">
        <div class="card-wrap">
          <div class="success-header-line"></div>
          <div class="pay-success-box" v-if="!isLogin && paySuccess">
            <i class="success-icon"></i>
            <span class="pay-success-first">{{ $t('order/pay.Success-1') }}</span>
            <span class="pay-success-sec">{{ $t('order/pay.Success-2') }} </span>
          </div>
          <div class="pay-success-box" v-if="(!isLogin && !paySuccess) || isLogin">
            <p class="success-title">{{ $t('order/pay.Title') }}</p>
            <p class="success-description">{{ $t('order/pay.Subtitle') }}</p>
            <div class="card-detail">
              <div v-if="paySuccess">
                <AccountAddress v-bind:userAccount="userAccount"></AccountAddress>
                <a-button class="start-btn" type="primary" @click="startNow()" :disabled="!gotTheDetail">
                  {{ '开始使用' }}
                </a-button>
              </div>
              <div v-if="!paySuccess">
                <div class="pay-sync" id="pay-sync">
                  <a-spin v-if="!paySuccess && !payFailed">
                    <a-icon slot="indicator" type="loading" style="font-size: 24px; color: #3963EF;" spin/>
                  </a-spin>
                  <a-icon v-if="paySuccess && !payFailed" type="check-circle" theme="twoTone" twoToneColor="#44B496"/>
                  <a-icon v-if="payFailed" type="close-circle" theme="twoTone" twoToneColor="#F2585E"/>
                  <span class="sync-result">{{ payStatus }}</span>
                </div>
                <p v-if="(!payFailed && isPaying) || paySuccess" class="pay-tips-center">{{ footerText }}</p>
                <p v-if="payFailed && isPaying" class="pay-tips-center">{{ $t('order/pay.Fail-2') }}
                  <a @click="openZendesk()">{{ $t('order/pay.Fail-3') }}</a>
                </p>
                <div class="header" :class="{'pay-success-header':paySuccess}">
                  <div class="btn-container" id="btn-container" v-if="gotTheDetail">
                    <a-button class="submit-btn" type="primary" id="submit-btn"
                              @click="payNow()" :disabled="!gotTheDetail">
                      {{ btnTips }}
                    </a-button>
                    <a class="submit-btn" style="position: absolute;background-color: transparent !important;"
                       v-if="!isPaying"
                       @click="payNow()"></a>
                    <a-button class="submit-btn2" type="primary" id="submit-btn2" ghost
                              @click="userPaid()">
                      {{ $t('order/pay.Button-1') }}
                    </a-button>
                  </div>
                </div>
                <div v-if="!gotTheDetail" class="package-list-loading">
                  <a-spin>
                    <a-icon slot="indicator" type="loading" :style="{'font-size': isPc ? '80px' : '60px'}" spin/>
                  </a-spin>
                </div>
                <div v-if="gotTheDetail" class="package-list">
                  <p class="package-title">{{ $t('order/pay.Bullet') }}</p>
                  <div class="list-item">
                    <p>{{ $t('order/pay.List-1') }}</p>
                    <p>{{ invoiceInfo.order_sn }}</p>
                  </div>
                  <div class="list-item">
                    <p>{{ $t('order/pay.List-2') }}</p>
                    <p>{{ invoiceInfo.shop }}</p>
                  </div>
                  <div class="list-item">
                    <p>{{ $t('order/pay.List-3') }}</p>
                    <p>{{ invoiceInfo.balance }} ￥</p>
                  </div>
                  <div class="list-item">
                    <p>{{ $t('order/pay.List-5') }}</p>
                    <p>{{ invoiceInfo.date }}</p>
                  </div>
                  <div class="list-item">
                    <p>{{ $t('order/pay.List-6') }}</p>
                    <p>{{ invoiceInfo.status }}</p>
                  </div>
                  <span class="check-invoice-detail" v-if="isLogin" @click="enterInvoiceDetail()">查看订单详情</span>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import {TweenMax} from 'gsap';
import AccountAddress from "../components/AccountAddress";
import {AU_AFF, AU_HMAC, AU_UUID, AU_IN_APP} from "@/store/Global";
import i18n from '../lang';

export default {
  name: 'payment',
  components: {
    AccountAddress
  },
  data() {
    return {
      userAccount: {},
      loading: false,
      resendTip: false,
      secondNum: 60,
      invoiceInfo: {},
      isAndroid: false,
      isIos: false,
      isPc: this.isPc(),
      isLogged: false,
      isLogin: false,
      isRenew: false,
      btnTips: this.$t('order/pay.Status-1'),
      payStatus: this.$t('order/pay.Paragraph-1'),
      paySuccess: false,
      payFailed: false,
      invoiceId: 0,
      intervalId: '',
      footerText: this.$t('order/pay.Paragraph-2'),
      payMethodList: [
        {
          id: 0,
          name: 'order/place:screen4.Tab1',
          logo: [{name: 'alipay', url: `pay/alipay.svg`}],
          type: 'pkjeub-alipay'
        }
      ],
      isSentData: false,
      payUrl: '',
      isPaying: false,
      gotTheDetail: false,
      userMaxRetry: 1,
      retryCount: 0,
      isUserRetry: false,
      selectedMethod: localStorage.getItem("payMethodIndex") ? Number(localStorage.getItem("payMethodIndex")) : 0,
      selectedOtherMethod: {},
      showMethodList: false,
      startPollingTime: 0,
      oldStatus: '',
      gtagData: undefined,
      inApp: localStorage.getItem(AU_IN_APP) === 'true',
    };
  },
  created() {
    this.isLogin = localStorage.getItem(AU_UUID) && localStorage.getItem(AU_HMAC);
    if (this.$route.query && this.$route.query.invoiceId) this.invoiceId = this.$route.query.invoiceId;
    if (this.$route.params.redirect) this.payUrl = this.$route.params.redirect;
    if (this.$route.params.gtagData) this.gtagData = this.$route.params.gtagData;
    if (this.$route.params.isRenew) this.isRenew = this.$route.params.isRenew;
    if (this.invoiceId !== 0) {
      this.getInvoiceDetail();
    }
  },
  mounted() {
  },
  destroyed() {
    window.clearInterval(this.intervalId);
  },
  methods: {
    enterInvoiceDetail() {
      let params = {
        invoiceId: this.invoiceId,
      };
      this.$router.push({
        path: 'user/invoice',
        name: 'invoiceDetail',
        params: params
      });
    },
    startNow() {
      window.open(`${VUE_SITE}?uuid=${localStorage.getItem(AU_UUID)}&hmac=${localStorage.getItem(AU_HMAC)}`, this.$inApp ? '_self' : '_blank');
    },
    payNow() {
      if (this.invoiceId != 0) {
        const that = this;
        if (this.isPaying) {// If is paying, that mean user trying repay
          if (that.payUrl) {
            window.open(that.payUrl, '_blank');
          } else {
            that.request.payInvoice(that.invoiceId).then(res => {
              if (res.code === 1) {
                if (res.url) {
                  that.payUrl = res.url;
                  window.open(that.payUrl, '_blank');
                }
              }
            })
          }
          if (that.isUserRetry) {// If payFailed before, need reset all flag for repay
            that.payStatus = that.$t('order/pay.Paragraph-1');
            that.payFailed = false;
            that.isUserRetry = false;
            that.retryCount = 0;
            that.startPolling();
          }
        } else if (this.paySuccess) {
          window.open(`${VUE_SITE}/my/service`, '_self')
          // } else if (this.intervalId.length > 0) {
        } else if (this.intervalId.length === 0) {
          this.isPaying = true;
          TweenMax.fromTo(document.getElementById('btn-container'),
              0.3,
              {marginTop: '0'},
              {
                css: {marginTop: that.isPc ? '30px' : '0.4rem'},
                onStart: function () {
                  TweenMax.fromTo(document.getElementById('submit-btn'),
                      0.4,
                      {
                        width: that.isPc ? '386px' : '7.867rem',
                        left: '0'
                      },
                      {
                        width: that.isPc ? '176px' : '3.68rem',
                        left: that.isPc ? '210px' : '4.187rem'
                      }
                  );
                  TweenMax.fromTo(document.getElementById('submit-btn2'),
                      0.4,
                      {width: that.isPc ? '386px' : '7.867rem'},
                      {
                        css: {width: that.isPc ? '176px' : '3.68rem'},
                        onComplete: function () {
                          that.btnTips = that.$t('order/pay.Button-2');
                          if (that.invoiceInfo.status !== i18n.t('Paid')) {
                            if (that.payUrl) {
                              window.open(that.payUrl, '_blank');
                            } else {
                              that.request.payInvoice(that.invoiceId).then(res => {
                                if (res.code === 1) {
                                  if (res.url) {
                                    that.payUrl = res.url;
                                    window.open(that.payUrl, '_blank');
                                  }
                                }
                              })
                            }
                          }
                        }
                      }
                  );
                }
              }
          );
          TweenMax.fromTo(document.getElementById('pay-sync'),
              0.4,
              {opacity: 0},
              {opacity: 1}
          );
          this.startPolling();
        } else {
          this.$notification.open({
            message: this.$t('order/pay.Fail-4'),
            onClick: () => {
            },
          });
        }
      } else {
        this.$notification.open({
          message: this.$t('order/pay.Fail-4'),
          onClick: () => {
          },
        });
      }
    },
    userPaid() {
      if (this.payFailed) {
        // If payFailed before, need to re-pay
        this.$notification.open({
          message: this.$t('order/pay.Fail-1'),
          onClick: () => {
          },
        });
      } else {
        this.payStatus = this.$t('order/pay.Status-3');
        this.getInvoiceDetail();
        this.isUserRetry = true
      }
    },
    getInvoiceDetail() {
      const that = this;
      this.request.getInvoiceDetail(this.invoiceId).then(res => {
        if (res.code === 1) {
          that.gotTheDetail = true;
          that.invoiceInfo.invoiceid = res.data.id;
          that.invoiceInfo.balance = res.data.amount;
          that.invoiceInfo.shop = res.data.goods.category.name + " - " + res.data.goods.name;
          that.invoiceInfo.order_sn = res.data.sn;
          that.invoiceInfo.duedate = res.data.created_at;
          that.invoiceInfo.date = res.data.created_at;
          that.invoiceInfo.status = i18n.t(res.data.status === 2 ? 'order/pay.Status-2' : 'order/pay.Item-6');
          that.invoiceInfo = Object.assign({}, that.invoiceInfo, '');
          if (res.data.status === 2) {
            if (that.oldStatus === 'Unpaid') {
              let exoclick = document.createElement("div");
              exoclick.setAttribute('style', 'display: none; visibility: hidden');
              exoclick.innerHTML = `<script  type="application/javascript" src="https://a.exoclick.com/tag_gen.js" data-goal="be98d96266307dc8cbedc8001331a95a"><` + `/script>`;
              document.body.appendChild(exoclick);
              that.oldStatus = data.data.data.status;
              that.pushPurchaseEvent(data);
            }
            window.clearInterval(that.intervalId);
            that.userAccount = {
              'address' : `${VUE_SITE}?uuid=${localStorage.getItem(AU_UUID)}&hmac=${localStorage.getItem(AU_HMAC)}`,
              'isFromPaymentPage' : true,
            };
            that.isPaying = false;
            that.paySuccess = true;

            if (window.InappWebInterface) {
              // Call Android interface
              let params = {
                'uuid': localStorage.getItem(AU_UUID),
                'hmac': localStorage.getItem(AU_HMAC),
              };
              window.InappWebInterface.webEvent('account', JSON.stringify(params));
            }
          } else if (that.isUserRetry) { // If this is user trigger to query
            // Set the max retry (can not just try once)
            if (that.retryCount > that.userMaxRetry) {
              // Pay Failed, need to re-pay
              window.clearInterval(that.intervalId);
              that.intervalId = '';
              that.payFailed = true;
              that.payStatus = that.$t('order/pay.Fail-1');
            }
            that.retryCount ++
          }
        } else {
          that.$notification.open({
            message: '获取订单详情失败，请稍后重试',
            onClick: () => {
            },
          });
        }
      });
    },
    pushPurchaseEvent(data) {
      let items = [];
      items.push({
        item_id: data.data.pid,
        item_name: data.data.name,
        promotion_id: 0,
        promotion_name: '',
        coupon: data.data.coupon,
        affiliation: localStorage.getItem(AU_AFF) ? localStorage.getItem(AU_AFF) : '',
        item_variant: data.data.cycle,
        price: this.isLogin ? data.data.total : data.data.subtotal,
        currency: 'USD',
      });
      let eventData = {
        affiliation: localStorage.getItem(AU_AFF) ? localStorage.getItem(AU_AFF) : '',
        coupon: data.data.coupon,
        currency: 'USD',
        items: items,
        transaction_id: data.data.transid,
        shipping: 0,
        value: this.isLogin ? data.data.total : data.data.subtotal,
        tax: 0,
        traffic_type: process.env.NODE_ENV === 'development' ? 'internal' : 'normal',
      }
      if (this.inApp) {
        // inApp event
        if (window.InappWebInterface) {
          // Call Android interface
          window.InappWebInterface.webEvent('purchase', JSON.stringify(eventData));
        }
      } else {
        // gtag('event', 'purchase', eventData)
      }
    },
    startPolling() {
      const that = this;
      if (that.intervalId.length === 0) {
        that.startPollingTime = new Date().getTime();
        that.intervalId = setInterval(function () {
          if (new Date().getTime() - that.startPollingTime > 1000 * 60 * 3) {
            window.clearInterval(that.intervalId);
            that.startPollingTime = new Date().getTime();
            that.intervalId = setInterval(function () {
              if (new Date().getTime() - that.startPollingTime > 1000 * 60 * 10) {
                window.clearInterval(that.intervalId);
                that.intervalId = '';
              } else {
                that.getInvoiceDetail();
              }
            }, 8000);
          } else {
            that.getInvoiceDetail();
          }
        }, 4000);
      }
    },
    openZendesk() {
    }
  }
};
</script>

<style lang="scss">
.interchange-container-pc {
  .success-content {
    display: flex;
    flex-direction: column;
    align-items: center;
    background: #F6F8FA;

    .steps-list-logged {
      width: 480px;
      margin-bottom: 40px;
      margin-top: 48px;
    }

    .steps-list {
      width: 752px;
      margin-bottom: 40px;
      margin-top: 48px;
    }

    .steps-icon {
      display: flex;
      flex-direction: row;
      justify-content: space-between;

      .pay-radio {
        z-index: 2;
        display: flex;
        width: 40px;
        height: 40px;
        align-items: center;
        justify-content: center;
        border-radius: 22px;
        border: 2px solid #E6EBF0;
        box-sizing: border-box;

        i {
          width: 24px;
        }

        &.hook {
          background: #3963EF;
          border-color: #3963EF;

          i {
            display: inline-block;
            content: '';
            height: 12px;
            background: url('https://cdn.jsdelivr.net/gh/UnPanel/unpanel-assets@v1.0.1/home/hook-white.svg') center no-repeat;
            background-size: contain;
          }
        }

        &.number {
          background: #3963EF;
          border-color: #3963EF;

          span {
            font-size: 18px;
            font-weight: 500;
            color: #fff;
          }
        }

        .hook-number {
          font-size: 18px;
          font-weight: 500;
          color: #fff;
        }
      }

      .pay-radio-gray {
        &.number {
          background: #C3CCD9;
          border-color: #C3CCD9;
        }
      }
    }

    .step-lines {
      width: 100%;
      position: relative;
      bottom: 21px;
      z-index: 1;
      display: flex;
      flex-direction: row;

      .blue-line {
        width: 50%;
        height: 2px;
        background-color: #3963EF;
      }

      .gray-line {
        background-color: #C3CCD9;
      }
    }

    .steps-line {
      height: 2px;
      width: 100%;
      background-color: #3963EF;
      position: relative;
      bottom: 21px;
      z-index: 1;
    }

    .steps-tips {
      font-size: 14px;
      color: #768392;
      margin-top: 12px;
    }

    .pay-success {
      align-items: flex-start;
      width: 1080px;
      //min-height: 100vh;
      display: flex;
      flex-direction: row;
      justify-content: center;
      background: #fff;

      .card-wrap {
        background-color: #fff;
        display: flex;
        flex-direction: column;
        align-items: center;
        margin: 60px 0;
        border: 1px solid #E6EBF0;
        border-radius: 8px;
        box-shadow: 0 2px 6px rgba(234, 238, 246, .6);
        justify-content: center;

        .success-icon {
          margin-top: 40px;
          margin-bottom: 28px;
          bottom: 192px;
          display: inline-block;
          width: 48px;
          height: 48px;
          background: url('https://cdn.jsdelivr.net/gh/UnPanel/unpanel-assets@v1.0.1/home/success.svg') center no-repeat;
          background-size: contain;
        }

        .success-header-line {
          height: 4px;
          width: 100%;
          background-color: #3963EF;
          border-top-left-radius: 8px;
          border-top-right-radius: 8px;
        }

        .pay-success-box {
          display: flex;
          flex-direction: column;
          align-items: center;
          justify-content: center;
          width: 467px;

          .pay-success-first {
            color: #383E44;
            font-size: 28px;
            margin-bottom: 12px;
          }

          .pay-success-sec {
            color: #768392;
            font-size: 16px;
            margin-bottom: 40px;
          }
        }

        .success-title {
          margin-top: 36px;
          margin-bottom: 12px;
          font-size: 32px;
          font-weight: 500;
          line-height: 33px;
          color: #383E44;
        }

        .success-description {
          font-size: 18px;
          color: #768392;
          margin-bottom: 40px;
        }

        .card-detail {
          border-top: 1px solid #E6EBF0;
          padding: 40px 40px 26px 40px;
          display: flex;
          flex-direction: column;
          width: 467px;
          align-items: center;

          .start-btn {
            margin-top: 40px;
            height: 48px;
            width: 386px;
            background-color: #3963EF;
            //margin-bottom: 24px;
            span {
              font-size: 18px;
            }
          }

          .pay-sync {
            position: absolute;
            opacity: 0;
            display: flex;
            width: 386px;
            flex-direction: row;
            justify-content: center;
            align-items: center;
            margin-bottom: 40px;

            .sync-result {
              margin-left: 8px;
              font-size: 18px;
              color: #383E44;
            }

            i {
              svg {
                width: 20px;
                height: 20px;
              }
            }
          }

          .pay-tips-center {
            margin-top: 40px;
            font-size: 14px;
            line-height: 20px;
            color: #A5B1BF;
            text-align: center;
          }

          .header {
            display: flex;
            flex-direction: column;
            align-items: center;
            margin-bottom: 50px;

            .btn-container {
              display: flex;
              justify-content: space-between;
              width: 386px;
              height: 48px;

              .submit-btn {
                position: relative;
                z-index: 2;
                height: 48px;
                width: 386px;
                background-color: #3963EF !important;

                span {
                  font-size: 18px;
                }
              }

              .submit-btn2 {
                position: absolute;
                z-index: 1;
                height: 48px;
                width: 386px;
                margin-bottom: 24px;
                color: #3963EF;
                border-color: #3963EF;

                &:hover {
                  color: #fff;
                  background-color: #3963EF !important;
                }

                span {
                  font-size: 18px;
                }
              }
            }

            .header-tips-footer {
              margin-top: 24px;
              font-size: 14px;
              line-height: 20px;
              color: #A5B1BF;
              margin-bottom: 40px;
            }

            .pay-method {
              position: absolute;
              margin-top: 75px;
              padding: 0;
              display: flex;
              flex-direction: column;
              justify-content: center;
              align-items: center;

              .select-other-way {
                display: flex;
                flex-direction: row;
                align-items: center;
                justify-content: center;

                p {
                  margin: 0rem;
                  color: #3963EF;
                  font-size: 14px;
                }

                .method-name-color {
                  color: #383E44;
                }

                i {
                  display: inline-block;
                  height: 20px;
                  margin-right: 6px;
                  background: no-repeat center;
                  background-size: contain;
                  margin: 0px 5px 0 10px;

                  &.alipay {
                    width: 58px;
                    background: url('https://cdn.jsdelivr.net/gh/UnPanel/unpanel-assets@v1.0.1/home/alipay.svg') center no-repeat;
                  }

                  &.wechatpay {
                    width: 64px;
                    background: url('https://cdn.jsdelivr.net/gh/UnPanel/unpanel-assets@v1.0.1/home/wechat-pay.svg') center no-repeat;
                  }

                  &.paypal {
                    width: 78px;
                    background: url('https://cdn.jsdelivr.net/gh/UnPanel/unpanel-assets@v1.0.1/home/paypal.svg') center no-repeat;
                  }

                  &.ic-unionpay {
                    width: 32px;
                    background: url('https://cdn.jsdelivr.net/gh/UnPanel/unpanel-assets@v1.0.1/home/ic-unionpay-new.svg') center no-repeat;
                  }

                  &.unionpay {
                    width: 28px;
                    background: url('https://cdn.jsdelivr.net/gh/UnPanel/unpanel-assets@v1.0.1/home/union-pay.svg') center no-repeat;
                  }

                  &.master {
                    width: 28px;
