<template>
    <div :class="{'create-account-box': isPc,'create-account-box-mobile': !isPc}">
        <div class="content-title">{{userAddress.length > 0 ? '下面地址打开即可登入账户，请务必妥善保存。 ' : '点击下面按钮，创建匿名账户。 '}}</div>
        <div class="content-left-box">
            <a-button class="btn-create-account" type="primary" v-if="userAddress.length === 0" @click="createAccount">创建账户</a-button>
            <div style="display:flex;flex-direction: column;" v-if="userAddress.length > 0">
                <span class="span-address">{{userAddress}}</span>
                <a-divider style="background-color: #DEE2E8;margin: 0px;"/>
                <div class="download-favorite-box">
                    <div class="item-box" @click="downloadAuAccountInfo">
                        <span class="download-span" v-if="isPc">{{'下载账户信息'}}</span>
                        <a-tooltip placement="top" trigger="click" :mouseLeaveDelay=0.5 :destroyTooltipOnHide=true>
                            <template slot="title">
                                <span>已复制</span>
                            </template>
                            <span class="download-span" v-if="!isPc" @click.stop="downloadAuAccountInfo()">{{'复制账户信息'}}</span>
                        </a-tooltip>
                    </div>
                    <a-divider class="divider" type="vertical"/>
                    <div class="item-box item-bookmark" v-if="isPc">
                        <span class="download-span favorite-span" style="color: #3963EF;">拖拽到书签栏</span><a :href="userAddress" style="position: absolute;opacity: 0.1" @click.prevent="preventOpenAUAdress">账户</a>
                        <!--<span class="bookmark-tips">將此按鈕拖到您的書籤欄</span>-->
                        <span class="login-tips-span" v-if="showBottomTips">您已登录，付款后该账户可直接使用新套餐。 </span>
                    </div>
                    <div class="item-box" v-if="!isPc" @click="addToDeviceDesktop()">
                        <span class="download-span favorite-span" style="color: #3963EF;">添加到手机桌面</span>
                    </div>
                </div>
            </div>
        </div>
        <span class="login-tips-span" v-if="showBottomTips && !isPc">您已登录，付款后该账户可直接使用新套餐。 </span>
    </div>
</template>

<script>
import {AU_HMAC, AU_SELECTED_SERVICE, AU_UUID} from "@/store/Global";

export default {
    name: 'CreateAccount',
    data() {
        return {
            isPc : this.isPc(),
            userAddress: '',
            showBottomTips: !this.$route.path.endsWith('order/pay') && localStorage.getItem(AU_UUID) && localStorage.getItem(AU_HMAC) && localStorage.getItem(AU_SELECTED_SERVICE)
        };
    },
    props: {
        userAccount: {},
    },
    watch: {
        userAccount: {
            immediate: true,
            handler(value) {
                if (value !== undefined) {
                    this.userAddress = value.address;
                    //this.showBottomTips = !this.$route.path.endsWith('order/pay') && localStorage.getItem(AU_UUID) && localStorage.getItem(AU_HMAC) && localStorage.getItem(AU_SELECTED_SERVICE);
                }
            }
        },
    },
    mounted(){
        if (!this.$route.path.endsWith('order/pay') && localStorage.getItem(AU_UUID) && localStorage.getItem(AU_HMAC)) {
            this.userAddress = `https://www.ausvc.link?uuid=${localStorage.getItem(AU_UUID)}&hmac=${localStorage.getItem(AU_HMAC)}`;
        }
    },
    methods: {
        preventOpenAUAdress() {

        },
        downloadAuAccountInfo(){
            if (this.isPc) {
                const element = document.createElement('a');
                element.setAttribute('href', 'data:text/plain;charset=utf-8,' + encodeURIComponent(`下面地址打开即可登入 ${SITE_NAME} 账户，请务必妥善保存。\n${this.userAddress}`));
                element.setAttribute('download', `${SITE_NAME} 账户地址`);
                element.style.display = 'none';
                element.click()
            } else {
                var textarea = document.createElement("textarea");
                textarea.value = this.userAddress;
                textarea.readOnly = true;
                document.body.appendChild(textarea);
                textarea.select(); // 选择对象
                document.execCommand("Copy"); // 执行浏览器复制命令
                textarea.className = "textarea";
                textarea.style.display = "none";
            }
        },
        addToDeviceDesktop() {

        },
        createAccount() {
            let param = {
                "recaptchaToken":"333333"
            };
            let that = this;
            this.$request.auCreatedNewAddress(param,function (data) {
                if (data && (data.status === 200 || data.status === 201) && data.data) {
                    localStorage.setItem(AU_UUID, data.data.uuid);
                    localStorage.setItem(AU_HMAC, data.data.hmac);
                    that.userAddress = data.data.userAddress;
                }
            });
        }
    }
};
</script>

<style lang="scss">
.create-account-box {
    text-align: left;
    .content-title {
        color: #242C45;
    }
    .content-left-box {
        margin-top: 20px;
        border: 1px solid #DEE2E8;
        border-radius: 4px;
        width: 386px;
        min-height: 122px;
        .btn-create-account {
            margin: 37px 137px;
            width: 112px;
            height: 48px;
            background: #3963EF;
        }
        .ant-btn:hover, .ant-btn:focus {
            color: white;
            background-color: #3963EF;
            border-color: #3963EF;
        }
        .span-address {
            margin:16px;
            text-align:left;
            word-wrap:break-word;
            word-break:break-all;
        }
        .download-favorite-box {
            display: flex;
            flex-direction: row;
            justify-content: space-between;
            cursor: pointer;
            .divider {
                height:48px;
                background-color: #DEE2E8;
                margin: 0px;
            }
            .item-box {
                width: 50%;
                height:48px;
                align-items: center;
                display: flex;
                justify-content: center;
                .download-span {
                    display: flex;
                    color: #3963EF;
                    &::before {
                        width: 20px;
                        height: 20px;
                        margin-right: 4px;
                        background: url('https://cdn.jsdelivr.net/gh/UnPanel/unpanel-assets@v1.0.1/home/ic-download.svg') center no-repeat;
                        background-size: contain;
                        content: '';
                    }
                }
                .favorite-span {
                    //color: rgba(255, 255, 255, 0.1);
                    &::before {
                        background: url('https://cdn.jsdelivr.net/gh/UnPanel/unpanel-assets@v1.0.1/home/ic-add.svg') center no-repeat;
                    }
                }
            }
            .item-bookmark {
                .bookmark-tips {
                    display: none;
                    position:absolute;
                    margin-top: 80px;
                    margin-left: -30px;
                    color: #616577;
                }
                .login-tips-span{
                    display: flex;
                    position:absolute;
                    margin-top: 110px;
                    margin-left: -275px;
                    color: #44B496;
                    align-items: center;
                    &::before {
                        width: 14px;
                        height: 14px;
                        margin-right: 8px;
                        background: url('https://cdn.jsdelivr.net/gh/UnPanel/unpanel-assets@v1.0.1/home/ic-green-tips.svg') center no-repeat;
                        background-size: contain;
                        content: '';
                    }
                }
                &:hover {
                    .bookmark-tips {
                        display: flex;
                    }
                    .login-tips-span{
                        display: none;
                    }
                }
            }
        }
    }
}
.create-account-box-mobile {
    text-align: left;
    .content-title {
        width: 100%;
        line-height: 0.533rem;
        color: #242C45;
        font-size: 0.373rem;
    }
    .login-tips-span{
        margin-top: 0.453rem;
        display: flex;
        font-size: 0.373rem;
        color: #44B496;
        align-items: center;
        &::before {
            width: 0.373rem;
            height: 0.373rem;
            margin-right: 0.213rem;
            background: url('https://cdn.jsdelivr.net/gh/UnPanel/unpanel-assets@v1.0.1/home/ic-green-tips.svg') center no-repeat;
            background-size: contain;
            content: '';
        }
    }
    .content-left-box {
        margin-top: 0.373rem;
        border: 0.013rem solid #DEE2E8;
        border-radius: 0.107rem;
        width: 100%;
        min-height: 2.88rem;
        display: flex;
        justify-content: center;
        align-items: center;
        .btn-create-account {
            width: 2.773rem;
            height: 1.067rem;
            background: #3963EF;
        }
        .ant-btn:hover, .ant-btn:focus {
            color: white;
            background-color: #3963EF;
            border-color: #3963EF;
        }
        .span-address {
            margin:0.373rem;
            font-size: 0.373rem;
            text-align:left;
            word-wrap:break-word;
            word-break:break-all;
        }
        .download-favorite-box {
            display: flex;
            flex-direction: row;
            justify-content: space-between;
            cursor: pointer;
            .divider {
                height: 1.173rem;
                background-color: #DEE2E8;
                margin: 0px;
            }
            .item-box {
                width: 50%;
                height: 1.173rem;
                align-items: center;
                display: flex;
                justify-content: center;
                .download-span {
                    display: flex;
                    color: #3963EF;
                    font-size: 0.373rem;
                    &::before {
                        width: 0.533rem;
                        height: 0.533rem;
                        margin-right: 0.107rem;
                        background: url('https://cdn.jsdelivr.net/gh/UnPanel/unpanel-assets@v1.0.1/home/ic-copy-address.svg') center no-repeat;
                        background-size: contain;
                        content: '';
                    }
                }
                .favorite-span {
                    font-size: 0.373rem;
                    &::before {
                        background: url('https://cdn.jsdelivr.net/gh/UnPanel/unpanel-assets@v1.0.1/home/ic-favorite.svg') center no-repeat;
                    }
                }
            }
        }
    }
}
</style>
